"use client";
import { useEffect, useState } from "react";
import { Heading, Text } from "@radix-ui/themes";
import Image from "next/image";
import { Navbar } from "@/components/modules/Navbar";

export default function Page() {
  const [user, setUser] = useState<string | null>(null);
  const [pullUps, setPullUps] = useState<string | null>(null);
  const [hangingPullUps, setHangingPullUps] = useState<string | null>(null);
  const [pushUps, setPushUps] = useState<string | null>(null);
  const [abs, setAbs] = useState<string | null>(null);
  const [longJump, setLongJump] = useState<string | null>(null);
  const [forwardBend, setForwardBend] = useState<string | null>(null);

  useEffect(() => {
    setUser(localStorage.getItem("user"));
    setPullUps(localStorage.getItem("Pull-ups"));
    setHangingPullUps(localStorage.getItem("Mark_HangingPull-ups"));
    setPushUps(localStorage.getItem("Mark_Push-ups"));
    setAbs(localStorage.getItem("Mark_ABS"));
    setLongJump(localStorage.getItem("Mark_longJump"));
    setForwardBend(localStorage.getItem("Mark_ForwardBend"));
  }, []);

  return (
    <>
      <Navbar type="Main" />
      <div className="flex flex-col gap-4">
        <div className="flex gap-2 items-center">
          <Heading>{user}</Heading>
          <Image
            src={"/Crown.svg"}
            width={16}
            height={16}
            alt="Crown"
            className="mt-1"
          />
        </div>
        <div className="bg-[#F3F7F9] rounded-xl flex w-full justify-between items-center px-4 py-3">
          <p>circle</p>
          <p>Средний рейтинг</p>
        </div>
        <div className="flex flex-col gap-3">
          <Text weight={"medium"}>Результаты:</Text>
          <div className="flex flex-col gap-2">
            {pullUps && (
              <div className="bg-[#F3F7F9] rounded-xl flex w-full justify-between items-center px-4 py-3">
                <p>Подтягивания из виса лежа на высокой перекладине</p>
                <p>{pullUps}</p>
              </div>
            )}

            {/* Добавь остальные блоки по аналогии */}
            {hangingPullUps && (
              <div className="bg-[#F3F7F9] rounded-xl flex w-full justify-between items-center px-4 py-3">
                <p>Подтягивания в висе</p>
                <p>{hangingPullUps}</p>
              </div>
            )}

            {pushUps && (
              <div className="bg-[#F3F7F9] rounded-xl flex w-full justify-between items-center px-4 py-3">
                <p>Отжимания</p>
                <p>{pushUps}</p>
              </div>
            )}

            {abs && (
              <div className="bg-[#F3F7F9] rounded-xl flex w-full justify-between items-center px-4 py-3">
                <p>Пресс</p>
                <p>{abs}</p>
              </div>
            )}
            {longJump && (
              <div className="bg-[#F3F7F9] rounded-xl flex w-full justify-between items-center px-4 py-3">
                <p>Прыжок в длину с места</p>
                <p>{longJump}</p>
              </div>
            )}

            {forwardBend && (
              <div className="bg-[#F3F7F9] rounded-xl flex w-full justify-between items-center px-4 py-3">
                <p>Наклон вперед</p>
                <p>{forwardBend}</p>
              </div>
            )}
          </div>
        </div>
      </div>
    </>
  );
}
